export default function BYDPage() {
  return (
    <main className="container mx-auto px-6 py-16">
      <h1 className="text-4xl font-bold mb-6">BYD Electric Vehicles</h1>
      <p className="text-muted-foreground mb-10 max-w-2xl">
        Explore our collection of modern electric vehicles imported directly
        from BYD — China’s leading EV manufacturer.
      </p>
      {/* You can later replace this with a grid of cars */}
      <div className="grid md:grid-cols-3 gap-8">
        <div className="border rounded-lg p-4">BYD Model A</div>
        <div className="border rounded-lg p-4">BYD Model B</div>
        <div className="border rounded-lg p-4">BYD Model C</div>
      </div>
    </main>
  );
}
